@import '../../styles/index';

.button {
  @include size(13rem, 2.8rem);
  border: 0;
  margin: 1.5rem 0 1rem;
  background: $white;
  border-radius: 0.3rem;
  cursor: pointer;
  font-weight: 700;
  outline: none;

  &:hover {
    background-image: $primary-gradient;
    color: $white;
  }
}
